import React from "react"
import loadable from '@loadable/component'
import * as Constants from "../constants/contants"

const Home = loadable(() => import('./home'))
const Portfolio = loadable(() => import('./portfolio'))
const CV = loadable(() => import('./cv'))
const Contact = loadable(() => import('./contact'))

export default class Main extends React.Component {
    constructor(props) {
        super(props);
    }
    render (){
        if (this.props.tab == Constants.home){
            return (
                <Home></Home>
            )
        }else{
            if (this.props.tab == Constants.portfolio){
                return (
                    <Portfolio></Portfolio>
                )
            }
            else{
                if (this.props.tab == Constants.cv){
                    return (
                        <CV></CV>
                    )
                }else{
                    return (
                        <Contact></Contact>
                    )
                }
            }
            
        }
    }
}